<template>
  <div id="category">
    <!--分类导航栏-->
    <nav-bar class="nav-bar">
      <div slot="center">商品分类</div>
    </nav-bar>
    <!--中间的主体内容-->
    <div class="content">
      <!--左侧的分类-->
      <tab-menu :categories="categories" @selectItem="selectItem"></tab-menu>
      <!--右侧的商品展示-->
      <div id="tab-content">
        <scroll class="tab-content"  ref="scroll" :probe-type="3" @scroll="caScroll" @pullingUp="loadMores" :pull-up-load="true">
           <tab-content-detail :category-detail="showGoods"></tab-content-detail>
        </scroll>
      </div>

    </div>

        <!--置顶按钮   监听组件根元素点击要用native-->
    <back-top @click.native="backClick" v-show="isShowBackTop"></back-top>
  </div>
</template>

<script>
  import NavBar from 'common/navbar/NavBar'
  import TabMenu from './childcomps/TabMenu'
  import TabContentDetail from './childcomps/TabContentDetail'
  import Scroll from 'common/scroll/Scroll'
  import {itemImageMixin, backTopMixin} from 'commons/mixin'
  import {getHomeGoods} from 'network/home'
  export default {
    name: 'Category',
    components: {
      NavBar,
      TabMenu,
      Scroll,
      TabContentDetail
    },
    data () {
      return {
        categories: [
          {
            title: '新品',
            name: 'news'
          },
          {
            title: '流行',
            name: 'pop'
          },
          {
            title: '精选',
            name: 'sell'
          },
          {
            title: '生日',
            name: 'birth'
          },
          {
            title: '寿糕',
            name: 'older'
          },
          {
            title: '卡通',
            name: 'katong'
          },
          {
            title: '水果',
            name: 'friut'
          },
          {
            title: '儿童',
            name: 'child'
          },
          {
            title: '植脂',
            name: 'zhizhi'
          },
          {
            title: '植脂',
            name: 'ruzhi'
          }
        ],
        goods: {
          'pop': {page: 0, list: []},
          'news': {page: 0, list: []},
          'sell': {page: 0, list: []},
          'birth': {page: 0, list: []},
          'older': {page: 0, list: []},
          'katong': {page: 0, list: []},
          'friut': {page: 0, list: []},
          'child': {page: 0, list: []},
          'zhizhi': {page: 0, list: []},
          'ruzhi': {page: 0, list: []} 
        },
        //用来接受子组件点击传递过来的数据类型
        currentType: 'news',
        saveY: 0
      }
    },
    //混入
    mixins: [itemImageMixin, backTopMixin],
    //页面活跃时调用
    activated () {
      //跳到离开时的位置
      this.$refs.scroll.scrollTo(0, this.saveY, 0);
      //刷新
      this.$refs.scroll.refresh()
    },
    mounted () {
      //接受改变商品收藏数量
      this.$bus.$on('changeCounts', (gid,gcollect,gkind) => {
        var goodsKind = ['pop','sell','news','birth','older','katong','friut','child','zhizhi','ruzhi'];
        //根据gid查找商品修改收藏数量
        for(var i = 0;i<goodsKind.length;i++) {
          let names = goodsKind[i];
          //判断种类
          if(names == gkind) {
          let g =this.goods[names].list;
          for(var j = 0;j < g.length;j++) {
            if(g[j].gid == gid) {              
              g[j].gcollect = gcollect;
            }
          }
          }
         
        }
        
      })
    },
    //页面离开时调用
    deactivated () {
      //保存离开时的位置
      this.saveY = this.$refs.scroll.getScrollY();
      //取消全局事件的监听
      this.$bus.$off('itemImageLoad', this.itemListener)
    },
    computed: {
      //点击分类
      showGoods() {
        return this.goods[this.currentType].list
      }
     
    },
    created () {
      //加载商品数据
      this.getHomeGoods(this.currentType);
    },
    methods: {
      //监听子组件传来的分类
      selectItem(name) {
        this.currentType = name
        this.getHomeGoods(this.currentType);
      },
      caScroll(position) {
        //决定置顶按钮是否显示
        this.isShowBackTop = (-position.y) > 700;
      },
      getHomeGoods(kind) {
        //获取要获取数据的页码
        const page = this.goods[kind].page + 1;
        const typepage = kind + "_" + page;
         getHomeGoods(typepage).then(res => {
           //将数据放到数组中
           this.goods[kind].list.push(...res);
           this.goods[kind].page += 1;
           //刷新
           this.$refs.scroll.finishPullUp();
        })
      },
       //上拉加载更多
      loadMores() {
        //监听图片的上拉  调用方法增加数据
          this.getHomeGoods(this.currentType);
        //刷新scroll
        this.$refs.scroll.refresh();  
      }
    }
  }
</script>

<style scoped>
  #category {
    height: 100vh;
    position: relative;
  }
  .nav-bar {
    background-color: #ff8198;
    font-weight: 700;
    color: #fff;
  }
  .content {
    height: cale(100% - 49px);
    position: relative;
    margin-top: 1px;
  }

  #tab-content {
    position: absolute;
    top: 5px;
    left: 80px;
    right: 0;
    bottom: -20px;
    overflow: hidden;
  }
  .tab-content{
    height: 100%;
  }
</style>