<template>
    <div class="menu-list">
      <div class="menu-list-item"
           :class="{active: index===currentIndex}"
           v-for="(item, index) in categories"
           :key="index"
           @click="itemClick(index)">
        {{item.title}}
      </div>
    </div>
</template>

<script>
import Scroll from 'common/scroll/Scroll'
  export default {
    name: 'TabMenu',
    components: {
		  Scroll
    },
    props: {
		  categories: {
        type: Array,
        default: []
      }
    },
    data () {
      return {
        //判断哪个分类处于活跃状态
        currentIndex: 0
      }
    },
    methods: {
      //点击事件
      itemClick(index) {
        //修改当前对应的下标
        this.currentIndex = index
        //向父组件发出事件 告诉当前处于哪个分类状态
        let name = this.categories[index].name;
        this.$emit('selectItem', name)
      }
    }
  }
</script>

<style scoped>
   .menu-list {
    background-color: #f6f6f6;
    height: 100%;
    width: 80px;
    box-sizing: border-box;
    overflow: hidden;
  }

  .menu-list-item {
    height: 45px;
    width: 80px;
    line-height: 45px;
    text-align: center;
    font-size: 14px;
  }

  .menu-list-item.active {
    font-weight: 700;
    color: #ff8198;
    background-color: #fff;
    border-left: 3px solid #ff8198;
  }
</style>